.root{
    overflow: hidden;
}
.container{
    display: -ms-flexbox;
    display: flex;
}
.container>*{
    -ms-flex-negative: 0;
        flex-shrink: 0;
}
.nav-box{
    line-height: 6.66667vw;
    text-align: center;
    background: #fff;
    font-size: 0;
}
.nav-item{
    display: inline-block;
    width:1.6vw;
    height: 1.6vw;
    background: #e8e8e8;
    border-radius: 50%;
    vertical-align: middle;
}
.nav-item-active{
    display: inline-block;
    width:8vw;
    height:1.6vw;
    background:#44aaeb;
    border-radius:1.33333vw;
    vertical-align: middle;
}
.nav-box>span+span{
    margin-left: 1.33333vw;
}


