.root{
    height: 100%;
    background: #fff;
}
.top{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    height:76.53333vw;
    background: url("../../assets/images/bg.png") no-repeat center;
    background-size: 100% 100%;
    text-align: center;
}
.logo-box{
    margin-bottom: 5.33333vw;
    width:26.66667vw;
    height: 26.66667vw;
    background: #fff;
    border-radius: 50%;
}
.logo{
    width:18.66667vw;
    height:21.86667vw;
    margin: 2.4vw 4vw;
}
.name{
    font-size: 8vw;
    color:#fff;
}
.form{
    width:72vw;
    margin: 0 auto;
    text-align: center;
}
.input-box{
    border-bottom: 1px solid #6793e5;
}
.input-box+.input-box{
    margin-top: 10.66667vw;
}
.input{
    width:62.66667vw;
    height:6.66667vw;
    padding: 1.33333vw 0 1.33333vw 9.33333vw;
    background: url("../../assets/images/user.png") no-repeat left center;
    background-size: 5.86667vw 5.86667vw;
    border:none;
    outline: none;
    color:#999;
}
.input-pwd{
    composes: input;
    background-image: url("../../assets/images/lock.png");
}
.submit-btn{
    display: block;
    width:100%;
    height: 11.73333vw;
    margin-top: 12vw;
    font-size:4.53333vw;
    color:#fff;
    background: #588ffc;
    border:1px solid #588ffc;
    border-radius: 1.06667vw;
    outline: none;
}
.forget{
    margin-top: 2.66667vw;
    font-size:3.2vw;
    color:#999;
}


